import React, { Component } from "react";
import "./dm.css";
class RazorpayPaymentButton extends Component {
  constructor(props) {
    super(props);
    this.amountInputRef = React.createRef();
    this.nameInputRef = React.createRef();
    this.emailInputRef = React.createRef();
    this.contactInputRef = React.createRef();
    this.state = {
      paymentStatus: null,
      showAnotherPage: false,
    };
  }

  handlePayment = () => {
    const options = {
      key: "rzp_test_N2Ou8LHYNBYXBP", // Replace with your Razorpay API key
      currency: "INR",
      name: "ISKCON Pandharpur",
      amount: 1000 * 100, // Convert the amount from rupees to paise
      description: "Sri Sri RadhaPandharinath",
      handler: function (response) {
        // Handle the successful payment response
        console.log(response);
        this.setState({ paymentStatus: "success" });
      }.bind(this),
      prefill: {
        email: this.emailInputRef.current.value,
        contact: this.contactInputRef.current.value,
      },
      notes: {
        
        name: this.nameInputRef.current.value,
      },
      onError: () => {
        this.setState({ paymentStatus: "failed" });
      },
    };

    const razorpayScript = document.createElement("script");
    razorpayScript.src = "https://checkout.razorpay.com/v1/checkout.js";
    razorpayScript.async = true;
    razorpayScript.payment_button_id="pl_M1tSKqA0TeBMS4";
    razorpayScript.onload = () => {
      const razorpay = new window.Razorpay(options);
      razorpay.open();
    };
    document.body.appendChild(razorpayScript);
  };

  render() {
    const { paymentStatus } = this.state;
    return (
      <div className="dm">
        {/* <h2>Payment Form</h2> */}
        {paymentStatus === "success" ? (
          <h1>test</h1>
        ) : paymentStatus === "failed" ? (
          <div>
            <h3>Payment Not Done. Please try again.</h3>
          </div>
        ) : (
          <form className="donation-manager">
            <div className="fdiv">
           
            </div>

            <div className="sdiv" >
            <label>Amount (in Rupees):</label>
              <input type="number" ref={this.amountInputRef} />
              <label>Name:</label>
              <input type="text" ref={this.nameInputRef} />
              <label>Email:</label>
              <input type="email" ref={this.emailInputRef} />
              <label>Contact:</label>
              <input type="text" ref={this.contactInputRef} />
            </div>
            <button className="userbtn" type="button" onClick={this.handlePayment}>
              Pay
            </button>
          </form>
        )}
      </div>
    );
  }
}

export default RazorpayPaymentButton;
