import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Db from "./about.json";
import "./about.css";

function Test() {
  const { id } = useParams();
  const [myData, setMyData] = useState([]);

  useEffect(() => {
    const fetchData = () => {
      const parsedId = parseInt(id);
      const item = Db.data.find((item) => item.id === parsedId);
      if (item) {
        setMyData([item]);
      } else {
        setMyData([]);
      }
    };

    fetchData();
  }, [id]);


  return (
    <div className="about">

      {myData.map((item) => (
        <div key={item.id}>
          <div className="test-first">
            <div className="about-image-container">
              {item.image && <img alt="Image1" src={item.image} />}
            </div>
            <div className="about-text-container">
              <h1 className="title">{item.Fheading}</h1>
              <p>{item.description}</p>
            </div>
          </div>
          <div className="test-second">
            <div className="about-image-container">
              {item.image1 && <img alt="Image2" src={item.image1} />}
            </div>
            <div className="about-text-container">
              <h1 className="title">{item.Sheading}</h1>
              <p>{item.description1}</p>
            </div>
          </div>
          {item.video && (
            <div>
              <h1 className="title">Video</h1>
              <div className="test-third">
                <iframe title="Video" src={item.video} frameBorder="0"></iframe>
              </div>
            </div>
          )}
          {item.website && (
            <div>
              <h1 className="title">Website</h1>
              <div className="test-third">
                <iframe title="Website" src={item.website} frameBorder="0"></iframe>
              </div>
            </div>
          )}
        </div>
      ))}
    </div>
  );
}

export default Test;
